  .main__container {
    // padding-top: 40px;
  }

  .main__catalog {
    display: grid;
    grid-template-areas: "post-1 post-2 post-3"
      "post-4 post-4 post-5"
      "post-4 post-4 post-6";
    grid-template-rows: repeat(3, 200px);
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 27px;

    h4 {
      margin: 0;
      text-transform: uppercase;
      color: $color-default-white;
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
    }

    a {
      margin-top: auto;
      border: 1px solid $color-default-white;

      position: relative;
      color: $color-default-white;
      font-weight: 700;
      font-size: 14px;
      line-height: 18px;

      letter-spacing: 1px;
      text-transform: uppercase;
      width: 180px;
      padding: 12px 12px 12px 30px;


      &::before {
        transform: rotate(180deg);
        position: absolute;
        content: " ";
        width: 16px;
        height: 18px;
        background-image: url("../img/sprite/icon-arrow.svg");
        top: calc(50%-9px);
        left: 3px;
      }

    }
  }

  .main__item {
    border-radius: 2px;
    background-color: $color-special-yellow;
    padding: 25px;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position-x: 90%;
    background-position-y: center;

    &:nth-child(2n) {
      background-color: $color-special-blue;
    }

    &:nth-child(3n) {
      background-color: $color-special-lilac;
    }

    &:nth-child(5n) {
      background-color: $color-special-greenlight;
    }

    &:last-of-type {
      background-color: $color-special-yellow;
    }

    &--materials {
      grid-area: post-1;
      background-image: url("../../img/sprite/icon-paints.svg");
    }

    &--instrument {
      grid-area: post-2;
      background-image: url("../../img/sprite/icon-instrument.svg");
    }

    &--technics {
      grid-area: post-3;
      background-image: url("../../img/sprite/icon-equipment.svg");
    }

    &--range {
      grid-area: post-4;
      padding: 0;
    }

    &--reduces {
      grid-area: post-5;
      background-image: url("../../img/sprite/icon-sale.svg");
    }

    &--delivery {
      grid-area: post-6;
      background-image: url("../../img/sprite/icon-delivery.svg");
    }
  }
